body {
  background-color: #eff3f8;
  margin: 0;
  padding: 0;
  font-family: "Kopub", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html,
#root {
  height: 100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
@font-face {
  font-family: Kopub;
  src: url(/movie_app/static/media/KoPubWorldLight.618f310c.otf) format("opentype");
  font-weight: 450;
}

@font-face {
  font-family: Kopub;
  src: url(/movie_app/static/media/KoPubWorldBold.31732071.otf) format("opentype");
  font-weight: 750;
}

.App {
  padding: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  font-size: 14px;
}

.Movie {
  background-color: white;
  width: 40%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-bottom: 50px;
  text-overflow: ellipsis;
  padding: 0 20px;
  box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3),
    0 5px 12px rgba(133, 133, 133, 0.22);
}

.Movie__Column {
  width: 30%;
  box-sizing: border-box;
  text-overflow: ellipsis;
}

.Movie__Column:last-child {
  padding: 20px 0;
  width: 60%;
}

.Movie h1 {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 5px;
}

.Movie .Movie__Genres {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-bottom: 10px;
}

.Movie__Genres .Movie__Genre {
  margin-right: 10px;
  color: #b4b5bd;
}

.Movie .Movie__Synopsis {
  line-height: 200%;
  text-overflow: ellipsis;
  color: #838697;
  font-size: 16px;
  overflow: hidden;
}

.Movie .Movie__Poster {
  max-width: 100%;
  position: relative;
  top: -20px;
  box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3),
    10px 15px 12px rgba(80, 80, 80, 0.22);
}
@media screen and (min-width: 320px) and (max-width: 667px) {
  .Movie {
    width: 100%;
  }
}

@media screen and (min-width: 320px) and (max-width: 667px) and (orientation: portrait) {
  .Movie {
    width: 100%;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .Movie__Poster {
    top: 0;
    left: 0;
    width: 100%;
  }
  .Movie__Column {
    width: 100% !important;
  }
}

